<!--
  @auth:yw
  echarts
-->

<template>
  <div
    v-loading="!params.data.length"
    :element-loading-background="$STANDARD.loadingBgColor"
    ref="chartEl"
    :style="{width : params.canvasStyle.width, height: params.canvasStyle.height}"
  ></div>
</template>

<script>
  import * as echarts from "echarts";
  import {gradientColor} from "@/utils/gradient";

  export default {
    name: "barline",
    props: {
      animateFlag: Boolean,
      params: {
        type: Object,
        default() {
          return {
            canvasStyle: {
              width: "1000px",
              height: "600px"
            },
            data: [],
            /*
               *false
               ****
               * */
            options: {
              mode: "abreast", //   abreast() | overlap() | stack()   ***
              overlap: "investigators", // ***mode'abreast' ()  key
              legendBgSrc: {
                // ***mode'abreast' () 
                //  ## ## (png,assets)
                // : avtive.png active
                active: "legendActive",
                inActive: "legendInactive"
              },
              colors: {
                //  STANDARD,standard.js,
                //
                bar: this.$STANDARD.gradientColors,
                line: this.$STANDARD.gradientColors
              },
              itemLabelColor: "#fff",//bar label
              rotate: 20,//x
              colorisarry: false,//,
              //colorsbox:[],//
              xName: "name", // ***key
              isVertical: true, // 
              nameLocation: true, //isVerticaltrue,Value
              splitLine: true, //isVerticaltrue,ValuesplitLine
              axisLabel: true, //isVerticaltrue,Valuelabel
              axisValuePosition: true, //isVerticaltrue,Value
              categoryInverse: true, // isVerticaltrue,
              axisValueShow: true, // value label  splitLine
              axisLineShow: true, // 
              axisLabelColor: " #fff",
              categoryBr: true, // label
              categoryCut: true, // label
              brNum: 2, // label 2
              barBorderRadius: true, //
              legendShow: true, // legend
              legendCut: true, // legend
              barLabelShow: true, // bar label  true
              barWidth: 10, // bar label 15
              top: "5%", // grid  5%
              left: "5%",
              right: "5%",
              bottom: "5%",
              legendPos: {
                //
                right: "5%",
                top: "5%"
              },
              legendW: 10, //legend  25
              legendH: 10, //legend  14
              legendGap: 10, //legend
              legendIcon: 'rect', // legend
              borderRadius: '50%', //legend
              map: {
                // ***
                num: "",
                age: ""
              },
              addTooltip: {  // legendtooltip
                "add_holder": ""
              },
              isLeftScale: true, // value
              isRightScale: true, // value
              //  line 
              axis1Name: "", // valuename ''
              axis2Name: "", // valuename ''
              splitLineShow: false, //valuesplitline
              line: ["age", "num"], // ***line key
              isAverage: true, // line markline
              lineWidth: 2, // line 1
              tooltipPercent: true, // tooltip  (,:|, 1234|23.32)
              tooltipShow: true,// tooltip
              iscolor: true,
              axisCategoryName: "", // name ''
              smooth: true, // 
              showSymbol: true, // 
              lineLabelShow: false, // label true
              symbolSize: 5, // 
              lineShadowBlur: 20, // 
              lineShadowColor: options.colors.line[1],  // 
              lineShadowOffsetY: 15, //
              lineOpacity: 0.5, // 
              tooltipType: "shadow", //  shadow || line
              axisLabelSize: 20, // label
              legendSize: 20, // label
            }
          };
        }
      }
    },
    data() {
      return {
        curCity: '',
        myChart: null,
        legendActive: true, //bar legend 
        animation: true
      };
    },
    mounted() {

      this.init();

      this.$once('hook:beforeDestroy', () => {
        if (this.interval) {
          clearInterval(this.interval);
        }
        this.myChart && this.myChart.dispose();
      })
    },
    methods: {
      init() {
        if (this.params.data.length === 0) {
          return;
        }

        if (this.myChart) {
          this.myChart.dispose();
          this.myChart = null;
        }
        this.curCity = this.params.curCity;
        let propData = [...this.params.data]; //
        let categoryData = []; //
        let options = this.params.options;
        //
        let linearGradient = options.isVertical ? [0, 1, 1, 0] : [0, 0, 0, 1];
        // keys(series)
        let dataKeys = Object.keys(options.map);
        // line
        let isAllLines = options.line
          ? options.line.length === dataKeys.length
          : false;
        // key(legend)
        this.legendData = Object.values(options.map);
        //legend
        if (options.mode === "overlap" && options.legendBgSrc) {
          let activeSrc = options.legendBgSrc.active;
          let inActiveSrc = options.legendBgSrc.inActive;
          this.activeLegendBg = require(`@/assets/${activeSrc}.png`);
          this.inActiveLegendBg = require(`@/assets/${inActiveSrc}.png`);

          this.legendData.forEach((item, index) => {
            if (options.map[options.overlap] === item) {
              this.legendData[index] = {
                name: item,
                icon:
                  this.params.legendActive === undefined
                    ? `image://${this.activeLegendBg}`
                    : this.params.legendActive[item]
                    ? `image://${this.activeLegendBg}`
                    : `image://${this.inActiveLegendBg}`
              };
            }
          });
        }

        //   let fixLegendData = [];
        //   legendData.forEach((item, index) => {
        //     if (options.legendCut && index == 1) {
        //       fixLegendData.push(item);
        //       fixLegendData.push(""); //
        //     } else {
        //       fixLegendData.push(item);
        //     }
        //   });

        this.serirsArr = [];
        let average = 0;
        let percentObj = {}; //line
        if (options.tooltipPercent) {
          // 
          propData.forEach(item => {
            percentObj[item[options.xName]] = [];
          });
        }

        // 
        if (options.tooltipPercent) {
          propData.forEach((item, index) => {
            //  
            let name = item[options.xName];
            for (let key in item) {
              //keyname  genre
              if (key !== "name" && key !== "genre") {
                percentObj[name].push(item[key].split("|")[1]);
              }
            }
          });
        }
        // linebarindex
        let lineIndex = 0,
          barIndex = 0,
          stack = 1;
        //series
        dataKeys.forEach((item, index) => {
          //series
          let data = [];
          let map = options.map;
          //
          propData.forEach(innerItem => {
            if (options.tooltipPercent) {
              data.push(innerItem[item].split("|")[0]);
            } else {
              data.push(innerItem[item]);
            }
          });
          //
          average = options.isAverage
            ? data.reduce((x, y) => +x + +y) / propData.length
            : 0;

          let series = {};
          //
          if (options.line && options.line.includes(item)) {
            // lineIndex = 0;
            //linelegend'','',''
            this.isRightAxis =
              map[item].endsWith("") ||
              map[item].endsWith("") ||
              map[item].endsWith("");
            series = {
              name: map[item],
              type: "line",
              yAxisIndex: isAllLines ? 0 : this.isRightAxis ? 1 : 0, //line y
              label: {
                show: false
              },
              symbolSize: options.symbolSize ? options.symbolSize : 5,
              symbol: "circle",
              showSymbol: options.showSymbol === undefined ? false : options.showSymbol, //
              smooth: options.smooth === undefined ? false : options.smooth, // 
              itemStyle: {
                normal: {
                  label: {
                    show:
                      options.lineLabelShow === undefined
                        ? true
                        : options.lineLabelShow,
                    fontSize: options.axisLabelSize ? options.axisLabelSize : 20,
                    formatter: this.isRightAxis ? "{c}%" : "{c}"
                  },
                  // shadowColor: options.gradientColor[index][1],
                  // shadowBlur: 15,
                  color:
                    options.colors && options.colors.line
                      ? this.$STANDARD.getColor(
                      options.colors.line[lineIndex],

                      [0, 1, 1, 0],
                      echarts
                      )
                      : ""
                },
                emphasis: {
                  label: {
                    show: true,
                    fontSize: options.axisLabelSize ? options.axisLabelSize : 20,
                  },
                  borderColor:
                    options.colors && options.colors.line
                      ? this.$STANDARD.colorToRgba(
                      options.colors.line[lineIndex],
                      0.3
                      )
                      : "rgba(255,255,255,.3)",
                  borderWidth: 20
                }
              },
              lineStyle: {
                //  x0, y0, x2, y2,  0 - 1 globalCoord  `true`
                color:
                  options.colors && options.colors.line
                    ? this.$STANDARD.getColor(
                    options.colors.line[lineIndex++],
                    // linearGradient,
                    [0, 0, 0, 1],
                    echarts
                    )
                    : "",
                width: options.lineWidth ? options.lineWidth : 3,
                shadowBlur: options.lineShadowBlur ? options.lineShadowBlur : 0,
                shadowColor: options.lineShadowColor ? options.lineShadowColor : 'none',
                shadowOffsetY: options.lineShadowOffsetY ? options.lineShadowOffsetY : 0,
                opacity: options.lineOpacity ? options.lineOpacity : 1,
              },
              //
              markLine: {
                symbolSize: 10,
                label: {
                  normal: {
                    show: true,
                    color: "#fff",
                    fontSize: 20
                  }
                },
                data: [
                  [
                    {
                      symbol: "line",
                      symbolSize: "0",
                      x: options.isAverage ? "6.7%" : "",
                      yAxis: average
                    },
                    {
                      symbol: "line",
                      symbolSize: "0",
                      x: "95%",
                      yAxis: average
                    }
                  ]
                ],
                lineStyle: {
                  normal: {
                    width: 2,
                    type: "solid",
                    color: "#FF507B"
                  }
                }
              },
              data: propData
            };
          } else {
            series = {
              name: map[item],
              type: "bar",
              stack: options.mode === "stack" ? stack : stack++,
              yAxisIndex: 0,
              xAxisIndex:
                options.mode === "overlap" && item === options.overlap ? 1 : 0,
              barWidth: options.barWidth ? options.barWidth : "auto",
              //series
              barGap: options.barGaps ? options.barGaps : "50%",
              //series
              // barCategoryGap: "50%",
              label: {
                show:
                  options.barLabelShow === undefined ? true : options.barLabelShow,
                position: options.barLabelOptions ? options.barLabelOptions : (options.isVertical ? options.categoryInverse ? 'left' : 'right' : ''),
                fontSize: options.axisLabelSize ? options.axisLabelSize : 20,
                textStyle: {
                  color: options.itemLabelColor ? options.itemLabelColor : "#fff"
                }
                // ,
              },
              itemStyle: {
                normal: {
                  barBorderRadius:
                    options.barBorderRadius === undefined
                      ? 0
                      : options.barBorderRadius,
                  color:
                    options.mode !== "overlap" || options.overlap !== item
                      ? options.colors && options.colors.bar
                      ? options.colorisarry ?
                        function (params) {
                          let colorList = options.colors.bar ?
                            options.colors.bar : [
                              "#fa2b7d",
                              "#ff5463",
                              "#ff874a",
                              "#f1825f",
                              "#c64aa7",
                              "#9a23e6",
                              "#7537fb",
                              "#5660fe",
                              "#4ac0ff",
                              "#4ae6ff"
                            ];
                          return colorList[params.dataIndex];
                        }
                        : this.$STANDARD.getColor(
                          options.colors.bar[barIndex++],
                          linearGradient,
                          echarts
                        )
                      : ""
                      : "transparent",
                  borderColor:
                    options.mode === "overlap" && options.overlap === item
                      ? options.colors && options.colors.bar
                      ? this.$STANDARD.getColor(
                        options.colors.bar[barIndex++],
                        linearGradient,
                        echarts
                      )
                      : ""
                      : "transparent",
                  borderWidth: 2,

                }
              },
              emphasis: {
                label: {
                  show: options.barLabelShow === undefined ? true : options.barLabelShow,
                  fontSize: options.axisLabelSize ? options.axisLabelSize : 20,
                },
                itemStyle: {
                  color: '#fefa82',
                  borderColor: '#000',
                  borderWidth: 0,
                  borderType: 'solid',
                  shadowOffsetX: 0,
                  shadowOffsetY: 0,
                  opacity: 1,
                },
              },
              data: this.params.data

            };

          }
          this.serirsArr.push(series);
        });

        //
        let axisValue = [
          {
            show: options.axisValueShow,
            type: "value",
            name: options.axis1Name ? options.axis1Name : "",
            nameLocation: options.nameLocation ? "start" : "end",
            nameTextStyle: {
              color: options.YaxisNameColor ? options.YaxisNameColor : "#ff517f",
              fontSize: options.axisLabelSize ? options.axisLabelSize : 14,
              fontWeight: 'bold',
              padding: [0, 45, 0, 0]
            },
            splitNumber: options.splitNumber ? options.splitNumber : 5,
            position: options.isVertical && options.axisValuePosition ? 'top' : 'bottom',
            inverse: options.categoryInverse,
            scale: options.isLeftScale ? true : false,
            axisTick: {
              show: false
            },
            axisLabel: {
              show: options.isVertical && options.axisLabel ? false : true,
              margin: 10,
              fontSize: 14,
              color: (value, index) => {
                let arr = [];
                let num = options.splitNumber ? options.splitNumber : 5;
                if (index < num) {
                  for (let i = 0; i < num; i++) {
                    arr.push(0)
                  }
                  let gradientC = gradientColor(arr, "#fdc48d", "#ff517f");
                  return gradientC(index)
                } else {
                  return "#ff517f"
                }
              },
              // color: options.axisLabelColor? options.axisLabelColor : this.$STANDARD(this.styleType).xAxisColor,
              // //
              // margin: 15,
              // fontSize: options.axisLabelSize ? options.axisLabelSize : 20,
            },
            axisLine: {
              show: false
            },
            splitLine: {
              show: options.isVertical && options.splitLine ? false : options.splitLineShow === undefined ? options.axisValueShow : options.splitLineShow,
              lineStyle: {
                type: 'dashed',
                color: "#2c344c" //
              }
            }
          },
          {
            show: options.axisValueShow,
            type: "value",
            name: options.axis2Name ? options.axis2Name : "",
            nameTextStyle: {
              color: this.$STANDARD.xAxisColor,
              fontSize: 20
            },
            scale: options.isRightScale ? true : false,
            axisTick: {
              show: false
            },
            axisLabel: {
              color: this.$STANDARD.xAxisColor,
              //
              margin: 15,
              fontSize: 20,
              formatter: this.isRightAxis ? "{value}%" : "{value}"
            },
            axisLine: {
              show: false
            },
            splitLine: {
              show: false,
              lineStyle: {
                color: this.$STANDARD.splitLineColor //
              }
            }
          }
        ];

        //
        for (let item of propData) {
          if (item.code) {
            categoryData.push({value: item[options.xName], id: item.code});
          } else {
            categoryData.push(item[options.xName]);
          }
        }
        // x15 dataZoom 
        let dataZoomShow = categoryData.length >= 100 ? true : false;
        if (options.isVertical) {
          dataZoomShow = false;
        }
        //
        let axisCategory = [
          {
            type: "category", //
            data: categoryData,
            inverse: options.isVertical ? true : false,
            axisTick: {
              show: false
            },
            position: options.categoryInverse ? "right" : "left",
            triggerEvent: true,
            name: options.axisCategoryName ? options.axisCategoryName : "",
            nameLocation: options.isVertical && options.nameLocation ? "start" : "end",
            nameTextStyle: {
              color: this.$STANDARD.xAxisColor,
              fontSize: options.axisLabelSize ? options.axisLabelSize : 20,
            },
            axisLabel: {
              interval: 0,
              rotate: options.rotate ? options.rotate : 0,
              // color: this.$STANDARD.xAxisColor,
              color: "#fff",
              inside: options.XasisLabelInside !== undefined ? options.XasisLabelInside : false,
              padding: options.XasisLabelPad ? options.XasisLabelPad : 0,
              fontSize: options.axisLabelSize ? options.axisLabelSize : 20,
              lineHeight: options.XaxisLabelLH ? options.XaxisLabelLH : 14,
              height: options.XaxisLabelH ? options.XaxisLabelH : 30,
              //
              margin: 15,
              formatter: params => {

                let res = "";
                let name = "";
                //label
                if (options.categoryBr) {
                  name = "";
                  let brNum = options.brNum ? options.brNum : 1;
                  for (let i = 0; i < params.length; i++) {
                    name += params[i];
                    if (i % brNum == brNum - 1) name += "\n";
                  }
                  res = name;
                } else {
                  res = params;
                }
                return res;
              },
              // rich: {
              //   value: {
              //     lineHeight: 16,
              //     fontSize: 20,
              //   }
              // }
            },
            //
            axisLine: {
              show: options.axisLineShow ? true : false,
              lineStyle: {
                color: "#a1a1b4"
              }
            },
            splitLine: {
              show: false,
              lineStyle: {
                color: this.$STANDARD.splitLineColor
              }
            }
          },
          {
            show: false,
            type: "category", //
            data: categoryData
          }
        ];

        // 
        let dataZoom = [
          {
            show: dataZoomShow,
            type: "slider",
            backgroundColor: "rgba(70,235,255,0.1)",
            fillerColor: "rgba(70,235,255,0.3)",
            borderColor: "rgba(70,235,255,0.6)",
            xAxisIndex: [0, 1],

            filterMode: "filter",
            start: 0,
            end: 100,
            textStyle: {
              color: "#fff"
            },
            handleIcon:
              "M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4v1.3h1.3v-1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z",
            handleSize: "80%",
            handleStyle: {
              color: "#fff",
              shadowBlur: 3,
              shadowColor: "rgba(0, 0, 0, 0.6)",
              shadowOffsetX: 2,
              shadowOffsetY: 2
            }
          }
        ];

        this.myChart = echarts.init(this.$refs.chartEl);
        this.option = {
          tooltip: {
            show: options.tooltipShow === undefined ? true : options.tooltipShow,
            trigger: "axis",
            backgroundColor: this.$STANDARD.tooltipBackgroundColor,
            borderColor: this.$STANDARD.tooltipBorderColor,
            // borderWidth: 1,
            padding: 12,
            axisPointer: {
              // 
              type: options.tooltipType ? options.tooltipType : "shadow" // 'line' | 'shadow'
            },
            formatter: function (params) {
              var result = params[0].name + "<br/>";

              if (options.mode === "overlap") {
                params.unshift(params.pop());
              }
              if (options.addTooltip) {
                let name = params[0].name;
                let dataItem = propData.filter(item => {
                  return item[options.xName] === name;
                })[0];
                for (const n in options.addTooltip) {
                  if (options.addTooltip.hasOwnProperty(n)) {
                    params.push({
                      seriesName: options.addTooltip[n],
                      value: dataItem[n]
                    });
                  }
                }
              }
              params.forEach(function (item, index) {
                let lastChar = item.seriesName.indexOf("") !== -1 || item.seriesName.indexOf("") !== -1;
                // "",""""
                if (lastChar) {
                  result += `${item.marker}${item.seriesName} : ${(item.value * 100).toFixed(2)}%</br>`;
                } else {
                  result += `${item.marker}${item.seriesName} : ${item.value}</br>`;
                }
              });
              return result;
            }
          },
          legend: {
            show: options.legendShow,
            data: this.legendData,
            itemGap: options.legendGap ? options.legendGap : 10,
            top: options.legendPos ? options.legendPos.top : "3%",
            right: options.legendPos ? options.legendPos.right : "5%",
            itemWidth: options.legendW ? options.legendW : 25,
            itemHeight: options.legendH ? options.legendH : 14,
            icon: options.legendIcon === "undefined" ? 'rect' : options.legendIcon,
            borderRadius: options.borderRadius ? options.borderRadius : 0,
            selected: this.params.legendActive ? this.params.legendActive : {},
            //   formatter: name => {
            //     let fix = "";
            //     fix = options.legendCut ? `{title|${name}}` : name;
            //     return fix;
            //   },
            textStyle: {
              fontSize: options.legendSize ? options.legendSize : 20,
              //
              // color: () => {
              //   return ''
              // },
              color: options.legendColor ? options.legendColor : this.$STANDARD.legendColor,
              rich: {
                title: {
                  width: 233,
                  fontSize: 20,
                  color: this.$STANDARD.legendColor
                }
              }
            }
          },
          grid: {
            left: options.left ? options.left : "5%",
            right: options.right ? options.right : "5%",
            top: options.top ? options.top : "5%",
            // bottom: options.bottom ? options.bottom : '5%',
            bottom: dataZoomShow ? "6%" : options.bottom ? options.bottom : '5%',
            containLabel: true,
          },
          label: {
            show: true,
            position: options.isVertical ? "right" : "top",
            distance: 10,
            color: this.$STANDARD.xAxisColor,
            fontSize: 20,
            zlevel: 10,
          },
          xAxis: options.isVertical ? axisValue : axisCategory,
          yAxis: options.isVertical ? axisCategory : axisValue,
          series: this.serirsArr,
          dataZoom: dataZoom,
          animation: this.animation,
          animationDelay: function (idx) {
            // 
            return idx * 50;
          }
        };
        // console.log(this.serirsArr);


        this.myChart.setOption(this.option);

        // 

        this.myChart.getZr().on('click', params => {
          const pointInPixel = [params.offsetX, params.offsetY];
          if (this.myChart.containPixel('grid', pointInPixel)) {
            let xIndex = this.myChart.convertFromPixel({seriesIndex: 0}, [params.offsetX, params.offsetY])[0];
            let yIndex = this.myChart.convertFromPixel({seriesIndex: 0}, [params.offsetX, params.offsetY])[1];
            this.animation=true;
            let name = propData[xIndex].name;
            this.$emit("departmentClick", name);
          }
        });

        this.legendselectchanged();
      },

      //bar legend 
      legendselectchanged() {
        this.myChart.on("legendselectchanged", params => {
          let options = this.params.options;
          let selected = params.selected;
          this.$emit("changeLegend", selected);

          if (options.mode === "overlap") {
            // legend namebar name && legend
            if (
              params.name === options.map[options.overlap] &&
              this.legendActive
            ) {
              this.legendActive = !this.legendActive;
              // this.imgSrc = require("@/assets/legendInactive.png");
              this.legendData.forEach((item, index) => {
                if (typeof item === "object") {
                  this.legendData[index].icon = `image://${
                    this.inActiveLegendBg
                    }`;
                }
              });
              this.myChart.setOption({
                legend: {
                  data: this.legendData
                }
              });
              // legend namebar name && legend
            } else if (
              params.name === options.map[options.overlap] &&
              !this.legendActive
            ) {
              this.legendActive = !this.legendActive;
              //this.imgSrc = require("@/assets/legendActive.png");
              this.legendData.forEach((item, index) => {
                if (typeof item === "object") {
                  this.legendData[index].icon = `image://${this.activeLegendBg}`;
                }
              });
              this.myChart.setOption({
                legend: {
                  data: this.legendData
                }
              });
            }
          }
        });
      },
      setHighBar() {
        this.animation=false;
        this.curCity = this.params.curCity;
        let obj = {color: '#fefa82'};
        this.serirsArr[0].data.forEach((item, index) => {
          if (this.curCity === item.name) {
            this.$set(item, "itemStyle", obj);
          } else {
            this.$delete(item, "itemStyle");
          }
        });
        this.myChart.setOption(this.option);
      }
    },
    watch: {
      animateFlag: {
        handler(newVal, oldVal) {
          this.init();
